import React from "react";
import "./sidebarnew.css";
import { Link } from "react-router-dom";

import HomeIcon from "@mui/icons-material/Home";
import MenuBookIcon from "@mui/icons-material/MenuBook";
import ReceiptIcon from "@mui/icons-material/Receipt";
import CoffeeIcon from "@mui/icons-material/Coffee";
import AutoGraphIcon from "@mui/icons-material/AutoGraph";
import CalculateIcon from "@mui/icons-material/Calculate";
import { useLocation } from "react-router-dom";
import { useContext, useEffect, forwardRef } from "react";
import { UserContext } from "../../UserContext";

export default function SideBarNew() {
  return (
    <>
      <div className="sideBarContainer">
        <div className="sideBarDescriptor">Dashboard</div>
        <div className="sideBarLinks">
          <Link to="/" className="link">
            <div className="sideBarLink">
              <HomeIcon className="sideBarIcon" />
              Home
            </div>
          </Link>
          <Link to="/orders" className="link">
            <div className="sideBarLink">
              <ReceiptIcon className="sideBarIcon" />
              Orders
            </div>
          </Link>
          <Link to="/calculations" className="link">
            <div className="sideBarLink">
              <CalculateIcon className="sideBarIcon" />
              Calculations
            </div>
          </Link>
          <Link to="/recipes" className="link">
            <div className="sideBarLink">
              <MenuBookIcon className="sideBarIcon" />
              Recipes
            </div>
          </Link>
          <Link to="/products" className="link">
            <div className="sideBarLink">
              <CoffeeIcon className="sideBarIcon" />
              Products
            </div>
          </Link>
          <Link to="/analytics" className="link">
            <div className="sideBarLink">
              <AutoGraphIcon className="sideBarIcon" />
              Analytics
            </div>
          </Link>
        </div>
      </div>
    </>
  );
}
